.content-box {
    vertical-align: middle;
    border: .1vh solid #222A37;
    border-radius: .75vh;
    cursor: pointer;
    width: 100%;
    height: 100%;
}

.background-image {
    z-index: -1;
    position: relative;
    width: 100%;
    height: 99.5%;
    border: .1vh solid #222A37;
    border-radius: .75vh;
    outline: none;
    opacity: .75;
    object-fit: cover;
}

.content-box:hover {
    animation: content_pulse 2s infinite;
}

.title-div {
    position: absolute;
    display: inline-flex;
    justify-content: center;
    text-align: center;
    margin: auto;
    color: white;
    border-radius: 1vh;
    border-top: solid .5vh #0984e3;
    border-bottom: solid .5vh #0984e3;
    width: 100%;
    background-color: rgba(34, 42, 55, .2);
    font-weight: 100;
}


h3 {
    font-size: 5vh;
    font-weight: 100;
}
