.body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Audiowide';
}

.page-display {
    width: 100%;
    height: 100%;
}

.body-container {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 100%;
    font-family: "audiowide";
}

.sidebar-nav {
    justify-content: space-evenly;
    align-items: center;
    width: 18vw;
    height: 89vh;
    border-right: 3px solid #0984e3;
    padding-right: 0.5vw;
    padding-left: 0.5vw;
}

.sidebar-nav button{
    width: 100%;
    margin: auto;
    height: 10%;
    font-size: 2vh;
    font-family: 'Audiowide';
    background-color: #0d1019;
    color: white;
    border: 1px solid #0984e3;
    border-radius: 3vh;
    margin-top: 1vh;
}

.middle-body-container {
    justify-content: center;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.org-profile-container {
    justify-content: center;
    text-align: center;
    width: 100%;
    height: auto;
}

.org-display-item {
    margin: auto;
    display: inline-block;
    justify-content: center;
    width: 100%;
    height: 100%;
    color: white;
    margin: 1.5vh;
}

.org-profile-description {
    display: inline-block;
    font-size: 5vh;
    margin-right: 1vw;
    color: rgb(138, 136, 136);
    
}

.org-profile-value {
    display: inline-block;
    font-size: 5vh;
    color: white;
}

.register-form {
    align-items: center;
    justify-content: space-evenly;
    text-align: center;
    width: 100%;
    height: 100%;
}

.submit-button {
    width: auto;
    height: 100%;
    font-size: 2.5vh;
    font-weight: 100;
    padding: 1vh;
    padding-left: 5vh;
    padding-right: 5vh;
    border-radius: 3vh;
    margin-top: 3vh;
    color: white;
    border: .05vh solid #0984e3;
    background-color: #353b48;
    font-family: "audiowide";
}


.alert-div {
    display: block;
    margin: auto;
    width: 100%;
    color: white;
    position: absolute;
    z-index: 1;
}